<template>
	<div id="mall-category">
		<public-header :title="title"></public-header>
		<public-footer :preLoading="showPreLoading" :menufooter="menufooter" v-if="menufooter && menufooter.data"></public-footer>
		<div class="container" :style="styleContent" :class="{'has-child-0': diydata.diy.storebasic_style == 2, 'has-child-4': diydata.diy.storebasic_style == 2 && diydata.categorySelected && diydata.categorySelected.child && diydata.categorySelected.child.length > 0 && diydata.categorySelected.child.length <= 4, 'has-child-8': diydata.diy.storebasic_style == 2 && diydata.categorySelected && diydata.categorySelected.child && diydata.categorySelected.child.length > 4}">
			<diy
				:diydata="diydata"
				:active = 'active'
				:is_grant = 'is_grant'
				:preLoading="showPreLoading"
				@onParentPlus="onParentPlus"
				@onParentSelectOption="onParentSelectOption"
				@onToggleCategory="onToggleCategory"
				@onOrderby="onOrderby"
				@onGetGoods="onGetGoods"
			>
			</diy>
		</div>

		<!--规格弹出层-->
		<van-popup v-model="ParentPopupSpecShow" class="options-popup" v-if="ParentPopupSpecShow">
			<div class="options-dialog">
				<div class="muti-close" @click="ParentPopupSpecShow = false"></div>
				<div class="muti-wrap">
					<div class="muti-food-title border-1px-b">
						{{goodsActive.title}}
					</div>
					<div class="muti-cont">
						<div class='muti-cont-inner'>
							<div class='muti-sec first' v-if="goodsActive.is_options == 1">
								<div class='muti-sec-title'>规格</div>
								<div class='muti-choice'>
									<template v-for="(item, index) in goodsActive.options">
										<div :class="{selected: (goodsActive.activeOptions.optionSelected == item.id)}" @click="onParentToggleOption(item.id, '', 'option')">{{item.name}}</div>
									</template>
								</div>
							</div>
							<div class='muti-sec' v-if="goodsActive.is_attrs == 1">
								<template v-for="(attrItem, attrIndex) in goodsActive.attrs">
									<div class='muti-sec-title'>{{attrItem.name}}</div>
									<div class='muti-choice'>
										<template v-for="(label, labelIndex) in attrItem.label">
											<div :class="{selected: (goodsActive.activeOptions.attrsSelected[attrIndex] == labelIndex)}" @click="onParentToggleOption(attrIndex, labelIndex, 'attr')">{{label}}</div>
										</template>
									</div>
								</template>
							</div>
						</div>
					</div>
					<div class="muti-choose">
						<div class='muti-oprt'>
							<div class='muti-cart-oprt clearfix'>
								<div class='add-food'>
									<van-icon name='plus' @click="onParentPlus($event, goodsActive,  '', '', '1')" style="background-color: #6BBA50;"></van-icon>
								</div>
								<template v-if="goodsSpecNum">
									<div class='food-num'>{{goodsSpecNum}}</div>
									<div class='remove-food'>
										<div class='remove icon icon-minus' @click="onParentMinus(goodsActive)"></div>
									</div>
								</template>
							</div>
						</div>
						<div class='muti-info'>
							<div class='muti-price'>{{Lang.dollarSign}}{{goodsActive.activeOption.price}}</div>
							<div class='muti-type'>({{goodsActive.activeOption.name}})</div>
						</div>
					</div>
				</div>
			</div>
		</van-popup>
		<!--SVIP弹出层-->
		<van-popup v-model="popupBuySvip" class="buy-svip">
			<div class='popup-svip-title'>客官，专享价限超级会员享受哦</div>
			<div class="svip-img">
				<div class="svip-vs-left">
					<i class="icon icon-crownfill"></i>
					<div class="font-12">会员专享价</div>
					<div class="svip-vs-price"><span class="font-14">{{Lang.dollarSign}}</span>{{goodsActive.svip_price}}</div>
				</div>
				<div class="svip-vs-right">
					<div class="svip-putong-title">普通用户价格</div>
					<div class="svip-putong-price"><span class="font-14">{{Lang.dollarSign}}</span>{{goodsActive.origin_price}}</div>
				</div>
			</div>
			<div class="buy-svip-text">即刻加入超级会员，尊享众多会员优惠</div>
			<button class="svip-price-btn font-16 margin-15-t">
				<div class="font-bold" @click="onParentPlus($event, goodsActive, '', '', '3')">会员专享价购买</div>
				<div class="font-12 padding-5-t">(订单结算时成为会员享优惠)</div>
			</button>
			<van-button size="normal" class="price-btn font-16 margin-15-t" @click="onParentPlus($event, goodsActive, '', '', '2')">原价购买</van-button>
		</van-popup>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import PublicFooter from '@/components/footer'
	import diy from '@/components/diy'
	import {mapState, mapMutations} from 'vuex'
	export default {
		data() {
			return {
				cid: 0,
				showPreLoading: true,
				styleContent: '#fff',
				is_grant: 0,
				title: '平台商品分类',
				active: 1,
				diydata: {
					diy: {
						data: {},
						fixedsearch: {},
						is_has_goodsTab: 0,
						is_has_location: 0,
						is_has_storebasic: 0,
						storebasic_style: 1,
					},
					superRedpacketData: {
						errno: 0,
						message: {},
						is_show: true
					},
					config: {},

					categorySelectedIndex: 0,
					categorySelectedId: 0,
					categoryAll:[],
					categorySelected: {
						child: [{}]
					},
					childSelectedIndex: 0,
					childSelectedId: 0,
					goodsAll: [],
					goodsItem: {
						orderby: {
							type: '',
							value: ''
						}
					},
					goodsLoading: true,
					//store: {},
					goodsHandleMode: 'all',
					vanColHeight: 667
				},
				//store: {},
				goodsActive: {},
				menufooter: {},
				ParentPopupSpecShow: false,
				popupBuySvip: false,
			}
		},
		components: {
			PublicHeader,
			PublicFooter,
			diy,
		},
		computed: {
			...mapState(['allcart', 'icart']),

			goodsSpecNum: function(){
				var cartIndex = this.goods.cartIndex;
				if(cartIndex === false) {
					if(this.allcart) {
						for(var i in this.allcart) {
							if(this.allcart[i].sid == this.goods.sid) {
								cartIndex = i;
							}
						}
					}
				}
				if(cartIndex === false) {
					return 0;
				}
				var icart = this.allcart[cartIndex]['cart'];
				if (!icart || !icart['data1'] || !icart['data1'][this.goodsActive.id]) {
					return 0;
				}
				var optionId = this.goodsActive.activeOptionId;
				if (!icart['data1'][this.goodsActive.id][optionId]) {
					return 0;
				}
				return icart['data1'][this.goodsActive.id][optionId]['num'];
			},
		},
		methods: {
			...mapMutations([
				'setLocation', 'getLocation', 'replaceAllcart'
			]),

			onLoad() {
				this.util.request({
					url: 'wmall/mall/category',
					data: {
						cid: this.cid,
						child_id: this.child_id,
						menufooter: 1
					},
				}).then((res) => {
					this.showPreLoading = false;
					this.diydata.goodsLoading = false;
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message);
						return;
					}
					this.diydata.config = result.message.config_mall;
					this.diydata.diy = this.util.extend(this.diydata.diy, result.message.diy);
					this.styleContent = {
						'background-color': this.diydata.diy.data.page.background
					};

					//this.util.setWXTitle(this.title);
					this.menufooter = window.menufooter;
					this.replaceAllcart(result.message.carts);

					//基础组件数据
					if(this.diydata.diy.is_has_storebasic == 1) {
						this.diydata.categoryAll = result.message.category;
						this.diydata.goodsItem = {
							page: 2,
							psize: 30,
							empty: 0,
							data: result.message.goods,
							orderby: {
								type: '',
								value: ''
							}
						};
						if(this.diydata.goodsItem.data.length < this.diydata.goodsItem.psize) {
							this.diydata.goodsItem.loaded = true;
							if(!this.diydata.goodsItem.data.length) {
								this.diydata.goodsItem.empty = 1;
							}
						}
						this.diydata.categorySelectedId = result.message.cid;
						this.diydata.childSelectedId = result.message.child_id;
						this.diydata.categorySelectedIndex = result.message.cindex;
						this.diydata.goodsAll[this.diydata.categorySelectedIndex] = [];
						this.diydata.goodsAll[this.diydata.categorySelectedIndex][this.diydata.childSelectedIndex] = this.diydata.goodsItem;
						this.diydata.categorySelected = result.message.category[this.diydata.categorySelectedIndex] || {};
					}

				})
			},

			onToggleCategory(params) {
				var cid = params.cid;
				var index = params.index;
				var from = params.from;
				var childIndex = 0;
				if(from == 'child') {
					childIndex = index;
					this.diydata.childSelectedIndex = childIndex;
					this.diydata.categoryAll[this.diydata.categorySelectedIndex]['childSelectedIndex'] = childIndex;
					this.diydata.categorySelected = this.diydata.categoryAll[this.diydata.categorySelectedIndex];
				} else {
					if(this.diydata.categoryAll[index]['childSelectedIndex'] > 0) {
						childIndex = this.diydata.categoryAll[index].childSelectedIndex;
					}
					this.diydata.categorySelected = this.diydata.categoryAll[index];
					this.diydata.categorySelectedIndex = index;
					this.diydata.childSelectedIndex = childIndex;
					this.diydata.categoryAll[index]['childSelectedIndex'] = childIndex;
				}
				this.diydata.categorySelectedId = this.diydata.categorySelected.id;
				this.diydata.childSelectedId = 0;
				if(this.diydata.categorySelected.child && this.diydata.categorySelected.child.length > 0) {
					this.diydata.childSelectedId = this.diydata.categorySelected.child[childIndex]['id'];
				}
				this.onGetGoods();
			},

			onGetGoods() {
				if(this.diydata.goodsLoading) {
					//return false;
				}
				if(!this.diydata.goodsAll[this.diydata.categorySelectedIndex]) {
					this.diydata.goodsAll[this.diydata.categorySelectedIndex] = [];
				}
				this.diydata.goodsItem = this.diydata.goodsAll[this.diydata.categorySelectedIndex][this.diydata.childSelectedIndex];
				if(!this.diydata.goodsItem) {
					this.diydata.goodsItem = {
						page: 1,
						psize: 30,
						empty: 0,
						loaded: false,
						data: [],
						orderby: {
							type: '',
							value: ''
						}
					};
				} else {
					if(this.diydata.goodsItem.empty) {
						return false;
					}
					if(this.diydata.goodsItem.loaded) {
						console.log('商品全部加载完成');
						return false;
					}
				}
				this.diydata.goodsLoading = true;
				this.util.request({
					url: 'wmall/mall/category/goods',
					data: {
						page: this.diydata.goodsItem.page,
						psize: this.diydata.goodsItem.psize,
						cid: this.diydata.categorySelectedId,
						child_id: this.diydata.childSelectedId,
						type: this.diydata.goodsItem.orderby.type,
						value: this.diydata.goodsItem.orderby.value
					},
				}).then((res) => {
					this.diydata.goodsLoading = false;
					var result = res.data.message.message;
					this.diydata.goodsItem.data = [...this.diydata.goodsItem.data, ...result.goods];
					this.diydata.goodsItem.page++;
					if(result.goods.length < this.diydata.goodsItem.psize) {
						this.diydata.goodsItem.loaded = true;
						if(!result.goods.length) {
							this.diydata.goodsItem.empty = 1;
						}
					}
					if(!this.diydata.goodsAll[this.diydata.categorySelectedIndex]) {
						this.diydata.goodsAll[this.diydata.categorySelectedIndex] = [];
					}
					this.diydata.goodsAll[this.diydata.categorySelectedIndex][this.diydata.childSelectedIndex] = this.diydata.goodsItem;
				})
			},

			onOrderby(params) {
				var type = params.type;
				var value = params.value;
				this.diydata.goodsItem = {
					page: 1,
					psize: 30,
					empty: 0,
					data: [],
					orderby: {
						type: type,
						value: value
					}
				};
				this.diydata.goodsAll[this.diydata.categorySelectedIndex][this.diydata.childSelectedIndex] = this.diydata.goodsItem;
				this.onGetGoods();
			},

			onParentPlus: function(event, goods, optionId, from, buysvip_status) {
				this.goodsActive = goods;
				var buysvip = 0;
				if(this.icart.pindan_id == 0) {
					if(this.icart.is_buysvip == 1) {
						buysvip = 1;
					} else {
						if(this.icart.svip_buy_show >= 1 && goods.svip_buy_show == 1) {
							if(buysvip_status == 1) {
								if(this.goodsActive.activeOption) {
									if(this.goodsActive.activeOption.origin_price > 0) {
										this.goodsActive.origin_price = this.goodsActive.activeOption.origin_price;
									} else {
										this.goodsActive.origin_price = this.goodsActive.activeOption.price;
									}
									if(this.goodsActive.activeOption.svip_price > 0) {
										this.goodsActive.svip_price = this.goodsActive.activeOption.svip_price;
									} else {
										this.goodsActive.svip_price = this.goodsActive.activeOption.price;
									}
								}
								this.popupBuySvip = true;
								return;
							} else if(buysvip_status == 2) {
								this.popupBuySvip = false;
							} else if(buysvip_status == 3) {
								this.popupBuySvip = false;
								buysvip = 1;
							}
						}
					}
				}
				var goods_id = goods.id || goods.goods_id;
				var option_id = goods.activeOptionId || optionId;
				var params = {
					sid: goods.sid,
					goods_id: goods_id,
					option_id: option_id,
					num: 1,
					sign: '+',
					is_buysvip: buysvip
				};
				this.util.request({
					url: 'wmall/mall/category/cart',
					data: params,
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message);
						return;
					}
					this.replaceAllcart(result.message.carts);
				})
			},

			onParentMinus: function(goods, optionId, from) {
				var buysvip = 0;
				if(this.icart.is_buysvip == 1) {
					buysvip = 1;
				}
				var goods_id = goods.id || goods.goods_id;
				var option_id = goods.activeOptionId || optionId;
				var params = {
					sid: goods.sid,
					goods_id: goods_id,
					option_id: option_id,
					num: 1,
					sign: '-',
					is_buysvip: buysvip
				};
				this.util.request({
					url: 'wmall/mall/category/cart',
					//showLoading: true,
					data: params,
				}).then((result) => {
					var result = result.data.message;
					if(result.errno) {
						this.$toast(result.message);
						return false;
					}
					this.replaceAllcart(result.message.carts);
				})
			},

			onParentSelectOption: function(goods) {
				this.goodsActive = goods;
				this.goods = goods;
				this.ParentPopupSpecShow = true;
				if(!this.goods['activeOptions']) {
					this.onParentGoodsOptionInit();
				}
			},

			onParentGoodsOptionInit: function() {
				this.goodsActive = this.goods;
				this.goodsActive['activeOptions'] = {
					option: 0,
					attrs: [],
					optionSelected: 0,
					attrsSelected: [],
					num: 0
				};
				if(this.goodsActive.is_options == 1) {
					this.goodsActive['activeOptions']['option'] = this.goodsActive.options[0]['id'];
					this.goodsActive['activeOptions']['optionSelected'] = this.goodsActive.options[0]['id'];
				}
				if(this.goodsActive.is_attrs == 1) {
					for(var i = 0; i < this.goodsActive.attrs.length; i++) {
						this.goodsActive['activeOptions']['attrs'].push(i + "s0");
						this.goodsActive['activeOptions']['attrsSelected'][i] = 0;
					}
				}
				this.onParentToggleActiveOption();
			},

			onParentToggleActiveOption: function(option_key) {
				var goodsActive = this.goodsActive;
				if(!option_key) {
					goodsActive['activeOptionId'] = goodsActive['activeOptions']['option'];
					if(goodsActive['activeOptions']['attrs'].length > 0) {
						goodsActive['activeOptionId'] = goodsActive['activeOptionId'] + '_' + goodsActive['activeOptions']['attrs'].join('v')
					}
				} else {
					goodsActive['activeOptionId'] = option_key;
				}
				goodsActive['activeOption'] = goodsActive['options_data'][goodsActive['activeOptionId']];
				goodsActive['activeOption']['num'] = this.parentGetSpecNum();
				this.goodsActive = Object.assign({}, goodsActive)
			},

			parentGetSpecNum: function() {
				var cartIndex = this.goods.cartIndex;
				if(cartIndex === false) {
					if(this.allcart) {
						for(var i in this.allcart) {
							if(this.allcart[i].sid == this.goods.sid) {
								cartIndex = i;
							}
						}
					}
				}
				if(cartIndex === false) {
					return 0;
				}
				var icart = this.allcart[cartIndex]['cart'];
				if (!icart || !icart['data1'] || !icart['data1'][this.goodsActive.id]) {
					return 0;
				}
				var optionId = this.goodsActive.activeOptionId;
				if (!icart['data1'][this.goodsActive.id][optionId]) {
					return 0;
				}
				return icart['data1'][this.goodsActive.id][optionId]['num'];
			},

			onParentToggleOption: function (optionId, optionValueID, type) {
				var goodsActive = this.goods;
				if(type == 'option') {
					goodsActive['activeOptions']['option'] = optionId;
					goodsActive['activeOptions']['optionSelected'] = optionId;
				} else {
					goodsActive['activeOptions']['attrs'][optionId] = optionId + 's' + optionValueID;
					goodsActive['activeOptions']['attrsSelected'][optionId] = optionValueID;
				}
				this.goodsActive = goodsActive;
				this.onParentToggleActiveOption();
			},
		},
		watch: {
			allcart() {
				var allcart = this.allcart;
				var categoryAllTemp = this.diydata.categoryAll;
				if(allcart) {
					for(var c in categoryAllTemp) {
						categoryAllTemp[c]['total'] = 0;
						if(categoryAllTemp[c]['child'] && categoryAllTemp[c]['child'].length > 0) {
							for(var index in categoryAllTemp[c]['child']) {
								categoryAllTemp[c]['child'][index]['total'] = 0;
								var childtotal = 0;
								var total = 0;
								for(var sid in allcart) {
									for(var i in allcart[sid]['cart']['data']) {
										for(var j in allcart[sid]['cart']['data'][i]) {
											if(allcart[sid]['cart']['data'][i][j]['plateform_cid'] == categoryAllTemp[c]['id']) {
												total += allcart[sid]['cart']['data'][i][j]['num'];
												categoryAllTemp[c]['total'] = total;
												if(allcart[sid]['cart']['data'][i][j]['plateform_child_id'] == categoryAllTemp[c]['child'][index]['id']) {
													childtotal += allcart[sid]['cart']['data'][i][j]['num'];
													categoryAllTemp[c]['child'][index]['total'] = childtotal;
												}
											}
										}
									}
								}

							}
						} else {
							var total = 0;
							for(var sid in allcart) {
								for(var i in allcart[sid]['cart']['data']) {
									for(var j in allcart[sid]['cart']['data'][i]) {
										if(allcart[sid]['cart']['data'][i][j]['plateform_cid'] == categoryAllTemp[c]['id']) {
											total += allcart[sid]['cart']['data'][i][j]['num'];
											categoryAllTemp[c]['total'] = total;
										}
									}
								}
							}

						}
					}
				} else {
					for(var c in categoryAllTemp) {
						categoryAllTemp[c]['total'] = 0;
						if(categoryAllTemp[c]['child']) {
							for(var index in categoryAllTemp[c]['child']) {
								categoryAllTemp[c]['child'][index]['total'] = 0;
							}
						}
					}
				}
				this.diydata.categoryAll = [...categoryAllTemp];
			},
			'$route' (to, from) {
				this.onLoad();
			}
		},
		created() {
			this.query = this.util.parseQuery(this.$route.query);
			this.cid = this.query.cid;
			this.child_id = this.query.child_id;
			this.onLoad();
		},
		mounted() {
			var that = this;
			let bodyHeight = document.documentElement.clientHeight;
			this.diydata.vanColHeight = bodyHeight - 46 - 50;
		}
	}
</script>

<style>
	#mall-category .container.has-child-4{
		padding-top: 151px;
	}
	#mall-category .container.has-child-8{
		padding-top: 205px;
	}
	#mall-category #public-header~.container.has-child-0{
		padding-top: 143px;
	}
	#mall-category #public-header~.container.has-child-4{
		padding-top: 197px;
	}
	#mall-category #public-header~.container.has-child-8{
		padding-top: 251px;
	}
	/*svip弹出层*/
	#mall-category .buy-svip{
		padding: 20px 15px;
		background-color: #fff;
		font-size: 15px;
		line-height: 1;
	}
	#mall-category .buy-svip .popup-svip-title{
		display: flex;
		align-items: center;
		font-weight: 700;
		white-space:nowrap;
		padding-bottom: 15px;
		text-align: center;
	}
	#mall-category .buy-svip .popup-svip-title:before,#mall-category .buy-svip .popup-svip-title:after{
		display: block;
		margin: 0 3.5px;
		width: 23px;
		height: 10px;
		background-image: url(../../../../static/img/svip-title-head.png);
		background-position: 0 0;
		background-size: 100% auto;
		background-repeat: no-repeat;
		content: "";
	}
	#mall-category .buy-svip .popup-svip-title:after{
		transform: scaleX(-1);
	}
	#mall-category .buy-svip .svip-img{
		display: flex;
		width: 100%;
		height: 73px;
		background: url(../../../../static/img/svip-vs-bg.png) no-repeat;
		background-size: 100% auto;
		margin-bottom: 15px;
	}
	#mall-category .buy-svip .svip-img .svip-vs-left{
		width: 50%;
		text-align: center;
		color: #FFE98B;
	}
	#mall-category .buy-svip .svip-img .svip-vs-left .icon{
		padding: 3px 0;
		font-size: 12px;
	}
	#mall-category .buy-svip .svip-img .svip-vs-left .svip-vs-price{
		font-size: 26px;
		padding: 5px 0;
	}
	#mall-category .buy-svip .svip-img .svip-vs-right{
		width: 50%;
		text-align: center;
		color: #656565;
	}
	#mall-category .buy-svip .svip-img .svip-vs-right .svip-putong-title{
		padding-top: 18px;
		font-size: 12px;
	}
	#mall-category .buy-svip .svip-img .svip-vs-right .svip-putong-price{
		font-size: 26px;
		padding: 5px 0;
	}
	#mall-category .buy-svip .buy-svip-text{
		font-size: 14px;
		color: #999;
		text-align: center;
	}
	#mall-category .buy-svip .svip-price-btn{
		display: block;
		width: 100%;
		height: 45px;
		text-align: center;
		background: linear-gradient(90deg,#faeaa7,#e0c470);
		border: none;
		border-radius: 3px;
		padding: 0;
		color: #7C4F0B;
	}
	#mall-category .buy-svip .price-btn{
		width: 100%;
		display: block;
		text-align: center;
		color: #7c7c7c;
		font-weight: 600;
	}
	#mall-category .buy-svip .van-button::before{
		border-color: transparent;
		background-color: transparent;
	}
	/*规格弹出层*/
	#mall-category .options-popup{
		max-width: 90%;
		width: 90%;
	}
	#mall-category .options-dialog{
		background-color: #fff;
		width: 100%;
	}
	#mall-category .options-dialog .muti-close{
		position: absolute;
		top: 10px;
		right: 10px;
		width: 24px;
		height: 24px;
		text-align: center;
	}
	#mall-category .options-dialog .muti-close::before,.options-dialog .muti-close::after{
		position: absolute;
		top: 10px;
		right: 1px;
		width: 20px;
		height: 1px;
		background: #666;
		content: '';
	}
	#mall-category .options-dialog .muti-close::before{
		-webkit-transform: rotate(45deg);
		-ms-transform: rotate(45deg);
		-moz-transform: rotate(45deg);
		transform: rotate(45deg);
	}
	#mall-category .options-dialog .muti-close::after{
		-webkit-transform: rotate(135deg);
		-ms-transform: rotate(135deg);
		-moz-transform: rotate(135deg);
		transform: rotate(135deg);
	}
	#mall-category .options-dialog .muti-food-title{
		line-height: 44px;
		margin: 0 35px;
		font-size: 16px;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		text-align: center;
		border-radius: 8px 8px 0 0;
	}
	#mall-category .options-dialog .muti-cont{
		max-height: 250px;
		overflow-y: scroll;
		overflow-x: hidden;
		-webkit-box-flex: 1;
		font-size: 14px;
		padding-bottom: 10px;
	}
	#mall-category .options-dialog .muti-cont .muti-sec .muti-sec-title{
		line-height: 20px;
		margin: 10px 0 0 15px;
	}
	#mall-category .options-dialog .muti-cont .muti-sec .muti-choice{
		margin: 0 5px;
		overflow: hidden;
	}
	#mall-category .options-dialog .muti-cont .muti-sec .muti-choice div{
		display: inline-block;
		float: left;
		margin: 10px 5px 0;
		min-width: 75px;
		padding: 0 10px;
		height: 28px;
		line-height: 26px;
		border: 1px solid #c4c4c4;
		box-sizing: border-box;
		border-radius: 14px;
		text-align: center;
		overflow: hidden;
	}
	#mall-category .options-dialog .muti-cont .muti-sec .muti-choice div.selected{
		border-color: #FF2D4B;
		color: #FF2D4B;
	}
	#mall-category .options-dialog .muti-choose{
		height: 56px;
		background-color: #f4f4f4;
		overflow: hidden;
		border-radius: 0 0 8px 8px;
	}
	#mall-category .options-dialog .muti-choose .muti-oprt{
		float: right;
		display: inline-block;
	}
	#mall-category .options-dialog .muti-choose .muti-cart-oprt{
		margin-top: 16px;
	}
	#mall-category .options-dialog .muti-choose .muti-cart-oprt .add-food,.options-dialog .muti-choose .muti-cart-oprt .remove-food{
		float: right;
		height: 35px;
		width: 35px;
		display: inline-block;
	}
	#mall-category .options-dialog .muti-choose .muti-cart-oprt .add-food{
		text-align: left;
	}
	#mall-category .options-dialog .muti-choose .muti-cart-oprt .remove-food{
		text-align: right;
	}
	#mall-category .options-dialog .muti-choose .muti-cart-oprt .add-food .van-icon{
		width: 30px;
		height: 30px;
		text-align: center;
		line-height: 30px;
		color: #fff;
		font-weight: bold;
		border-radius: 100%;
		position: relative;
		font-size: 16px;
		background: -webkit-gradient(linear,left top, right top,from(#FF930F),to(#FF5E4E));
		background: linear-gradient(to right,#FF930F,#FF5E4E);
	}
	#mall-category .options-dialog .muti-choose .muti-cart-oprt .remove-food div{
		width: 30px;
		height: 30px;
		text-align: center;
		line-height: 30px;
		color: #fff;
		font-weight: bold;
		border-radius: 100%;
		position: relative;
		font-size: 16px;
		color: #ff2d4b;
		background-color: #fff;
		border: 1px solid #ff2d4b;
	}
	#mall-category .options-dialog .muti-choose .muti-cart-oprt div{
		height: 27px;
		line-height: 27px;
		min-width: 21px;
		text-align: center;
		float: right;
	}
	#mall-category .options-dialog .muti-choose .muti-info{
		margin-left: 15px;
		line-height: 56px;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}
	#mall-category .options-dialog .muti-choose .muti-info .muti-price{
		font-size: 24px;
		color: #FB4E44;
		vertical-align: middle;
		display: inline-block;
	}
	#mall-category .options-dialog .muti-choose .muti-info .muti-type{
		margin-left: 5px;
		font-size: 12px;
		color: #2F2F2F;
		display: inline-block;
		vertical-align: middle;
	}
</style>


